export default function uiBbHeaderDirective($interpolate, $compile) {
  return {
    restrict: 'E',
    scope: {
      headingType: '=',
    },
    link: ($scope, $element) => {
      $scope.$watch('tagName', () => {
        const content = $element.html();
        const header = $interpolate(`
          <{{ tagName }} 
            role="heading"
            data-role="headings" 
            class="mb-3"
            data-ng-bind="'{{ content }}'"
           ></{{ tagName }}>`)({ tagName: $scope.headingType, content });
        const e = $compile(header)($scope);

        $element.contents().remove();
        $element.append(e);
      });
    },
  };
}



