import angular from 'vendor-bb-angular';
import uiBbHeaderDirective from './directive';

/**
 * @name ui-bb-header-ng
 * @type {string}
 * @description Dynamic header element directive
 */
export default angular
  .module('ui-bb-header-ng', [])
  .directive('uiBbHeaderNg', ['$interpolate', '$compile', uiBbHeaderDirective])
  .name;



